.table {
  margin-top: 140px;
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
  padding-left: 0px;
  width: 100%;
  gap: 20px;
  /* height: 80vh; */
  position: absolute;
  z-index: 0;

  padding-bottom: 5px;
  background-color: transparent;
}
.Title {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: transparent;
  font-family: "Times New Roman", Times, serif;
  align-items: center;
  color: rgb(230, 9, 230);
  position: fixed;
  height: 60px;
  font-size: 20px;
  z-index: 10;
}
