.card {
  list-style-type: none;
  width: 100%;
  max-width: 300px;
  height: 160px;
  background-color: white;
  color: white;
  border-color: palevioletred;
  border-left: 2.5px solid rgb(255, 3, 255);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
}

.dataRow {
  font-family: serif;
  width: 100%;
  background-color: transparent;
  font-size: 16px;
  height: 30px;
  margin: 0px;
  color: black;
  display: flex;
  justify-content: space-between;

  align-items: center;
}

.titSPanPatients {
  display: flex;
}
.titlePatientvitalrecord {
  margin-right: 10px;
  background-color: transparent;
}
.buttonsIcon {
  width: 100%;
  background-color: transparent;
  display: flex;
  justify-content: end;
}
