.containerMenu-items {
  margin-top: 75px;
  width: 90%;
  /* height: 150px; */
  background-color: white;
  border-radius: 30px;
  position: fixed;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation-name: fade;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  z-index: 3;
  padding-top: 10px;
  padding-bottom: 10px;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 2;
  }
}
.itemMenu {
  width: 90%;
  background-color: white;
  height: 40px;
  font-size: 20px;
  border-color: transparent;
  font-family: "Times New Roman", Times, serif;

  color: rgb(241, 112, 245);
}
