.contheaderAndmenu {
  width: 100%;
  display: flex;
  justify-content: center;
}

.header-mobile {
  width: 100%;
  /* background-color: rgb(241, 112, 245); */
  background-color: white;
  height: 60px;
  position: fixed;
  display: flex;
  justify-content: center;
  z-index: 9;
}

.sync {
  width: 35px;
}

.check {
  width: 40px;
  background-color: white;
}

.ul-mobile {
  padding: 0px;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  list-style: none;
  align-items: center;
  color: white;
  margin: 0px;
  text-align: start;
  gap: 10px;
}
.li-logo {
  width: 20%;
  text-align: start;
  background-color: transparent;
}

.li-menu {
  width: 20%;
  text-align: end;
  background-color: transparent;
}

.image {
  width: 35px;
}
.logo {
  margin-top: 0px;
  width: 60px;
}
.title_header {
  width: 40%;
  color: rgb(230, 9, 230);
  text-align: center;
  font-family: "Times New Roman", Times, serif;
  font-size: 20px;
}
