.searchContainer {
  width: 100%;
  position: fixed;
  top: 60px;
  z-index: 2;
  background-color: white;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: start;
}

.searchBars {
  padding-left: 20px;
  padding-right: 20px;
  height: 50px;
  border-radius: 15px;
  background-color: white;
  width: 90%;
  max-width: 350px;
  font-size: 17px;
}
.search {
  width: 80%;
  max-width: 300px;
  height: 40px;
  /* padding-left: 10px; */
  background-color: white;
  border: 1px solid;
  color: rgb(255, 3, 255);
  outline: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;

  border-right: 0px;
}
.search:focus {
  border-color: #3498db; /* Change border color on focus */
  box-shadow: 0 0 5px #3498db; /* Add a box shadow on focus */
}
