.card {
  list-style-type: none;
  width: 100%;
  max-width: 300px;
  height: 160px;
  background-color: white;
  color: white;
  border-color: palevioletred;
  /* background-image: linear-gradient(
    to right,
    rgb(234, 7, 188),
    rgb(159, 7, 210)
  ); */
  border-left: 2.5px solid rgb(255, 3, 255);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
}
span {
  font-size: 16px;
  font-weight: bold;
  font-family: serif;
}
.ptNme {
  font-family: serif;
  width: 100%;
  background-color: transparent;
  font-size: 16px;
  height: 30px;
  margin: 0px;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ManagePatient {
  width: 100%;
  background-image: linear-gradient(
    to right,
    rgb(234, 7, 188),
    rgb(159, 7, 210)
  );
  color: white;
  height: 35px;
  border-color: transparent;
  border-width: 0px;
  border-radius: 10px;
}
